import {makeAutoObservable} from 'mobx'
import moment from 'moment'

export default class GuildModel {
  store: any
  wid: string
  _id: string
  guildName: string
  guildLevel: string
  guildIconIndex: string
  outNotice: string
  inNotice: string
  bIsShowList: string
  exp: number
  number: number
  guildContribution: number
  subscribeLv: string
  subscribeAble: boolean
  memberCount: number
  master: string
  submaster: string
  createdAt: string
  members: string[]
  constructor(
    store: any,
    {_id, wid, members, master, submaster, createdAt, data}: any,
    idx: number
  ) {
    makeAutoObservable(this)
    this.store = store
    this._id = _id
    this.wid = wid
    this.number = idx
    this.guildName = data.options.guildName
    this.guildLevel = data.options.guildLevel
    this.guildIconIndex = data.options.guildIconIndex
    this.outNotice = data.options.outNotice
    this.inNotice = data.options.inNotice
    this.bIsShowList = data.options.bIsShowList
    this.exp = data.options.exp
    this.subscribeLv = data.options.subscribeLv
    this.subscribeAble = data.options.subscribeAble
    this.memberCount = data.options.memberCount
    this.master = master
    this.submaster = submaster
    this.members = members
    this.guildContribution = 0
    this.createdAt = moment(createdAt).format('YYYY.MM.DD HH:mm:ss')
  }
}
