import {action, makeAutoObservable, when, toJS} from 'mobx'
import {makePersistable, isHydrated} from 'mobx-persist-store'
import {getConsts} from '../apis/const'
import RewardModel from 'stores/models/RewardModel'
import {sendMail} from 'apis/player'

type objectType = {
  [key: string]: any
}

export type SeverType = 'unity' | 'test' | 'release'

class AppStore {
  isCollapsed: boolean
  accessToken: string
  stringData: objectType
  gameItemData: objectType
  equipmentKeyList: objectType
  userResourceReward: objectType
  heroData: objectType
  userInfo: {id: string; remember: boolean}
  server: {wid: string; cid: string}
  serverType: SeverType
  rewards: RewardModel[]
  rewardObj: object
  constructor() {
    makeAutoObservable(this)
    makePersistable(this, {
      name: 'AppStore',
      properties: [
        'isCollapsed',
        'accessToken',
        'userInfo',
        'server',
        'serverType',
      ],
      storage: window.localStorage,
    })
    this.isCollapsed = false
    this.accessToken = ''
    this.userInfo = {id: '', remember: false}
    this.stringData = {}
    this.gameItemData = {}
    this.equipmentKeyList = {}
    this.userResourceReward = {}
    this.heroData = {}
    this.rewards = []
    this.rewardObj = {}
    this.serverType = 'unity'
    this.server = {
      wid: '',
      cid: '',
    }

    when(
      () => Boolean(this.accessToken),
      () => {
        this.getDR_StringData().then(() => {
          this.getDR_GameItemData()
          this.getDR_EquipmentData()
          this.getDR_HeroData()
          this.getDR_RewardData()
        })
      }
    )
  }

  setServerType = (type: SeverType) => {
    this.serverType = type
  }

  getIsConfirmForRelease = () => {
    if (this.serverType === 'release') {
      if (!window.confirm('릴리즈 서버입니다. \r\n계속하시겠습니까?')) {
        return false
      }
    }

    return true
  }

  setIds = (w: any, c: any) => {
    this.server = {
      wid: w?._id,
      cid: c?._id,
    }
  }

  getDR_StringData = () => {
    return getConsts({name: 'DR_StringData'}).then(({data}) => {
      this.stringData = data.value
    })
  }

  getDR_GameItemData = async () => {
    this.gameItemData = await this.getData('DR_GameItemData')
  }

  getDR_EquipmentData = async () => {
    this.equipmentKeyList = await this.getData('DR_EquipmentData')
  }

  getDR_HeroData = async () => {
    this.heroData = await this.getData('DR_HeroData')
  }

  getDR_RewardData = async () => {
    this.userResourceReward = await getConsts({name: 'DR_RewardData'})
  }

  getData = (name: string) => {
    return getConsts({name}).then(({data}) => {
      const {value} = data
      for (let key in value) {
        if (value.hasOwnProperty(key)) {
          const item = value[key]
          value[key].value = item.key
          value[key].label = this.stringData[item.displayName || item.name]
            ? this.stringData[item.displayName || item.name].kor
            : item.displayName || item.name
        }
      }

      return value
    })
  }

  getName = (key: string) => {
    return this.stringData[key]
  }

  @action setIsCollapsed = (isCollapsed: boolean) => {
    this.isCollapsed = isCollapsed
  }

  @action login = (
    userInfo: {id: string; remember: boolean},
    token: string
  ) => {
    this.userInfo = userInfo
    this.accessToken = token
  }

  @action logout = () => {
    this.accessToken = ''
  }

  initRewards = () => {
    this.rewards = []
  }

  addReward = (reward: RewardModel) => {
    this.rewards.push(new RewardModel(this, reward))
  }

  sendMailReward = ({data, value}: any) => {
    this.rewardObj = {}
    this.rewards.forEach((r) => r.toJSON())

    return sendMail({
      ...data,
      content: {...value, ...toJS(this.rewardObj)},
    })
  }

  get isHydrated() {
    return isHydrated(this)
  }
}

export default new AppStore()
