import React, {useEffect, useState} from 'react'
import {Input, Select, Space} from 'antd'
import Title from '../Text/Title'
import StyledButton from '../Button/StyledButton'

const GuildSearch = ({
  onSearch = (searchType: string, searchValue: string) => {},
  onlyPid = false,
  defaultSearchValue = '',
}) => {
  const [searchType, setSearchType] = useState('data.options.guildName')
  const [searchValue, setSearchValue] = useState(defaultSearchValue)

  useEffect(() => {
    setSearchValue(defaultSearchValue)
  }, [defaultSearchValue])
  return (
    <Space>
      <Title style={{marginRight: 15}}>길드 검색</Title>
      <Select
        style={{width: 150}}
        defaultValue={'data.options.guildName'}
        value={searchType}
        onChange={(v) => setSearchType(v)}
      >
        <Select.Option value={'data.options.guildName'}>길드명</Select.Option>
        <Select.Option value={'master'}>길드장</Select.Option>
      </Select>
      <Input
        placeholder={'입력하세요.'}
        value={searchValue}
        onChange={(e) => setSearchValue(e.target.value)}
      />
      <StyledButton blue onClick={() => onSearch(searchType, searchValue)}>
        검색
      </StyledButton>
    </Space>
  )
}

export default GuildSearch
