import React from 'react'
import {Route, Switch} from 'react-router-dom'
import LoginPage from './pages/LoginPage'
import AdminUserPage from './pages/admin/AdminUserPage'
import ServerPage from './pages/admin/ServerPage'
import PlayerPage from './pages/player/PlayerPage'
import './App.less'
import PlayersPage from './pages/player/PlayersPage'
import NoticePage from './pages/message/NoticePage'
import ChatPage from './pages/message/ChatPage'
import MailPage from 'pages/message/MailPage'
import RankPage from './pages/data/RankPage'
import RewardPage from './pages/data/RewardPage'
import {observer} from 'mobx-react-lite'
import {useStores} from './stores'
import GuildsPage from './pages/player/GuildsPage'
import GuildPage from './pages/player/GuildPage'
import CheckPage from './pages/manage/CheckPage'

const App: React.FC = () => {
  const {appStore} = useStores()
  return appStore.isHydrated ? (
    <Switch>
      <Route exact path={'/'}>
        <AdminUserPage />
      </Route>
      <Route exact path={'/login'}>
        <LoginPage />
      </Route>
      <Route exact path={'/admin/user'}>
        <AdminUserPage />
      </Route>
      <Route exact path={'/admin/server'}>
        <ServerPage />
      </Route>
      <Route exact path={'/manage/check'}>
        <CheckPage />
      </Route>
      <Route exact path={'/player/one'}>
        <PlayerPage />
      </Route>
      <Route exact path={'/player/many'}>
        <PlayersPage />
      </Route>
      <Route exact path={'/player/guild/:guildName?'}>
        <GuildPage />
      </Route>
      <Route exact path={'/player/guilds'}>
        <GuildsPage />
      </Route>
      <Route exact path={'/message/notice'}>
        <NoticePage />
      </Route>
      <Route exact path={'/message/mail'}>
        <MailPage />
      </Route>
      <Route exact path={'/message/chat'}>
        <ChatPage />
      </Route>
      <Route exact path={'/data/rank'}>
        <RankPage />
      </Route>
      <Route exact path={'/data/reward'}>
        <RewardPage />
      </Route>
    </Switch>
  ) : null
}

export default observer(App)
