import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {useLocation} from 'react-router-dom'
import qs from 'qs'
import {Tabs, Divider, Space, Pagination, Popover, Table} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import Panel from '../../components/Layout/Panel'
import ServerSearch from '../../components/Search/ServerSearch'
import PlayerSearch from '../../components/Search/PlayerSearch'
import Title from '../../components/Text/Title'
import StyledButton from '../../components/Button/StyledButton'
import {useStores} from '../../stores'
import PlayerDesc from '../../components/Description/PlayerDesc'
import {PlayerType} from '../../stores/models/PlayerModel'
import MailForm from '../../components/Form/MailForm'
import {CloseSquareOutlined, MailOutlined} from '@ant-design/icons'
import Resources from '../../components/player/Resources'
import Items from '../../components/player/Items'
import Equipments from 'components/player/Equipments'
import Heros from '../../components/player/Heros'
import Adventures from '../../components/player/Adventures'
import Agit from 'components/player/Agit'
import {findUser} from 'apis/player'
import Log from 'components/player/Log'
import moment from 'moment'

const AdminUserPage = () => {
  const [visible, setVisible] = useState(false)
  const [visibleLog, setVisibleLog] = useState(false)
  const [visibleRemoveMail, setVisibleRemoveMail] = useState(false)
  const [removeMails, setRemoveMails] = useState<any>([])
  const {playerStore} = useStores()
  const location = useLocation()
  const {pid = ''}: any = qs.parse(location.search, {ignoreQueryPrefix: true})
  const {
    currentPlayer,
    playerList,
    setCurrentPlayer,
    getPlayer,
    getPlayerList,
    removeMail,
  } = playerStore

  useEffect(() => {
    playerStore.currentPlayer = undefined
    if (pid) {
      setTimeout(() => {
        getPlayer({pid})
      }, 1000)
    }
  }, [])

  return (
    <BasicLayout>
      <TitlePanel>유저 관리</TitlePanel>
      <Panel>
        <ServerSearch isOnlyServer />
        <Divider />
        <PlayerSearch
          defaultSearchValue={pid}
          onSearch={(searchType, searchValue) => {
            if (searchType === 'pid') {
              getPlayer({pid: searchValue, cid: 'all'})
            }
            if (searchType === 'nickname') {
              getPlayerList('', 1, {nickname: searchValue, cid: 'all'})
            }
            if (searchType === 'uid') {
              findUser({mid: searchValue}).then(({data}) => {
                return getPlayerList('', 1, {
                  uid: data._id,
                  mid: searchValue,
                  cid: 'all',
                })
              })
            }
          }}
        />
        {currentPlayer && (
          <div style={{marginTop: 30}}>
            <Space>
              <Title style={{marginRight: 15}}>
                기본 정보 {currentPlayer.mailbox.length}
              </Title>
              <Popover
                placement="bottomRight"
                content={
                  <div
                    style={{
                      width: 600,
                      border: '2px solid #bababb',
                    }}
                  >
                    <div
                      style={{
                        background: '#fafafa',
                        display: 'flex',
                        padding: '10px 20px 0',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                      }}
                    >
                      <Space align={'baseline'}>
                        <MailOutlined
                          style={{
                            fontSize: 20,
                            color: '#a7a7a7',
                            marginRight: 5,
                          }}
                        />
                        <h2>우편 보내기</h2>
                      </Space>
                      <span>
                        <CloseSquareOutlined
                          onClick={() => setVisible(false)}
                          style={{fontSize: 20, color: '#a7a7a7'}}
                        />
                      </span>
                    </div>
                    <div style={{padding: '0 16px 20px'}}>
                      <MailForm
                        players={[currentPlayer]}
                        onClose={() => setVisible(false)}
                      />
                    </div>
                  </div>
                }
                trigger="click"
                visible={visible}
                onVisibleChange={setVisible}
              >
                <StyledButton purple>우편 보내기</StyledButton>
              </Popover>
              <Popover
                placement="bottomRight"
                content={
                  <div
                    style={{
                      width: 800,
                      border: '2px solid #bababb',
                    }}
                  >
                    <div
                      style={{
                        background: '#fafafa',
                        display: 'flex',
                        padding: '10px 20px 0',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                      }}
                    >
                      <Space align={'baseline'}>
                        <MailOutlined
                          style={{
                            fontSize: 20,
                            color: '#a7a7a7',
                            marginRight: 5,
                          }}
                        />
                        <h2>우편 회수(삭제)</h2>
                        <StyledButton
                          purple
                          onClick={() => {
                            if (removeMails.length > 0) {
                              if (
                                window.confirm(
                                  '해당 우편을 정말 삭제하겠습니까?'
                                )
                              ) {
                                removeMail(currentPlayer.pid, removeMails).then(
                                  () => {
                                    alert('삭제하였습니다.')
                                    setVisibleRemoveMail(true)
                                  }
                                )
                              }
                            }
                          }}
                        >
                          삭제하기
                        </StyledButton>
                      </Space>
                      <span>
                        <CloseSquareOutlined
                          onClick={() => setVisibleRemoveMail(false)}
                          style={{fontSize: 20, color: '#a7a7a7'}}
                        />
                      </span>
                    </div>
                    <div style={{padding: '0 16px 20px'}}>
                      <Table
                        rowKey={'mid'}
                        bordered
                        rowSelection={{
                          onChange: (selectedRowKeys: React.Key[]) => {
                            setRemoveMails(selectedRowKeys)
                          },
                        }}
                        columns={[
                          {title: 'mid', dataIndex: 'mid'},
                          {
                            title: 'content',
                            dataIndex: 'content',
                            render: (v) => {
                              return JSON.stringify(v)
                            },
                          },
                          {
                            title: 'createdAt',
                            dataIndex: 'createdAt',
                            render: (v) => {
                              return moment(v).format('YYYY.MM.DD HH:mm:ss')
                            },
                          },
                        ]}
                        dataSource={currentPlayer.mailbox}
                        pagination={{position: ['bottomCenter']}}
                      />
                    </div>
                  </div>
                }
                trigger="click"
                visible={visibleRemoveMail}
                onVisibleChange={setVisibleRemoveMail}
              >
                <StyledButton purple>우편 회수(삭제)</StyledButton>
              </Popover>
              <StyledButton gray onClick={() => currentPlayer.block()}>
                {currentPlayer.isBlock ? '계정 정지 해지' : '계정 정지'}
              </StyledButton>
              <StyledButton
                gray
                onClick={() =>
                  currentPlayer.changePlayerType(PlayerType.계정해지)
                }
              >
                계정 해지
              </StyledButton>
              <StyledButton
                yellow
                onClick={() => currentPlayer.changePlayerType(PlayerType.VIP)}
              >
                VIP 지정
              </StyledButton>
              <StyledButton
                yellow
                onClick={() => currentPlayer.changePlayerType('')}
              >
                VIP 해제
              </StyledButton>
              <Popover
                placement="bottomRight"
                content={
                  <div
                    style={{
                      width: 600,
                      border: '2px solid #bababb',
                    }}
                  >
                    <div
                      style={{
                        background: '#fafafa',
                        display: 'flex',
                        padding: '10px 20px 0',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                      }}
                    >
                      <Space align={'baseline'}>
                        <MailOutlined
                          style={{
                            fontSize: 20,
                            color: '#a7a7a7',
                            marginRight: 5,
                          }}
                        />
                        <h2>플레이어 로그</h2>
                      </Space>
                      <span>
                        <CloseSquareOutlined
                          onClick={() => setVisibleLog(false)}
                          style={{fontSize: 20, color: '#a7a7a7'}}
                        />
                      </span>
                    </div>
                    <div style={{padding: '0 16px 20px'}}>
                      <Log pid={currentPlayer?.pid} />
                    </div>
                  </div>
                }
                trigger="click"
                visible={visibleLog}
                onVisibleChange={setVisibleLog}
              >
                <StyledButton green>로그 보기</StyledButton>
              </Popover>
              <StyledButton
                red
                onClick={() => {
                  if (
                    window.confirm(
                      '해당 유저를 계정 정지 후, 게임 접속을 종료하겠습니까?'
                    )
                  ) {
                    currentPlayer?.kick().then(() => alert('완료하였습니다.'))
                  }
                }}
              >
                KICK
              </StyledButton>
            </Space>
            <div style={{marginTop: 30}}>
              <PlayerDesc player={currentPlayer} />
              <div style={{textAlign: 'center', marginTop: 20}}>
                <Pagination
                  size={'small'}
                  total={playerList.length}
                  pageSize={1}
                  onChange={(page: number) => setCurrentPlayer(page)}
                />
              </div>
            </div>
          </div>
        )}
      </Panel>
      {currentPlayer && (
        <Content>
          <Tabs type={'card'} style={{marginTop: 50}}>
            <Tabs.TabPane tab="재화" key="재화">
              <Resources
                currentPlayer={currentPlayer}
                userResources={currentPlayer.userResources}
              />
            </Tabs.TabPane>
            <Tabs.TabPane tab="아이템" key="아이템">
              <Items
                currentPlayer={currentPlayer}
                items={currentPlayer.gameItems}
              />
            </Tabs.TabPane>
            <Tabs.TabPane tab="장비" key="장비">
              <Equipments
                currentPlayer={currentPlayer}
                items={currentPlayer.equipments}
              />
            </Tabs.TabPane>
            <Tabs.TabPane tab="영웅" key="영웅">
              <Heros items={currentPlayer.heros} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="모험" key="모험">
              <Adventures items={[currentPlayer.adventures]} />
            </Tabs.TabPane>
            <Tabs.TabPane tab="던전" key="던전">
              <Agit items={currentPlayer.agit} />
            </Tabs.TabPane>
          </Tabs>
        </Content>
      )}
    </BasicLayout>
  )
}

export default observer(AdminUserPage)
