import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {useLocation, useParams} from 'react-router-dom'
import qs from 'qs'
import {Divider, Table, Descriptions} from 'antd'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import Panel from '../../components/Layout/Panel'
import ServerSearch from '../../components/Search/ServerSearch'
import {useStores} from '../../stores'
import GuildSearch from '../../components/Search/GuildSearch'
import * as playerApi from '../../apis/player'

const GuildPage = () => {
  const {playerStore, appStore} = useStores()
  const [members, setMembers] = useState<any>([])
  const location = useLocation()
  const params: any = useParams()
  const {pid = ''}: any = qs.parse(location.search, {ignoreQueryPrefix: true})
  const {currentGuild, getGuildList} = playerStore

  const columns = [
    {title: 'no', dataIndex: 'no'},
    {
      title: '직위',
      dataIndex: '_id',
      render: (id: string) => {
        if (currentGuild?.master === id) {
          return '길마'
        }
        if (currentGuild?.submaster === id) {
          return '부길마'
        }
        return ''
      },
    },
    {title: '이름', dataIndex: 'nickname'},
    {title: '공헌도', dataIndex: 'guildContribution'},
    {title: '길드 코인', dataIndex: 'guildCoin'},
  ]

  useEffect(() => {
    playerStore.currentPlayer = undefined
    if (params.guildName) {
      getGuildList(1, {
        query: {'data.options.guildName': {$eq: params.guildName}},
      })
    }
  }, [])

  useEffect(() => {
    if (currentGuild?.members) {
      Promise.all(
        currentGuild.members.map((pid) =>
          playerApi.getPlayer({
            wid: appStore.server.wid,
            cid: null,
            pid,
          })
        )
      ).then((response) => {
        setMembers(
          response.map(({data}: any, idx) => {
            return {
              ...data,
              no: idx + 1,
              key: data._id,
              nickname: data.profile.nickname,
              guildContribution: data.profile.guildContribution,
              guildCoin: data.data.userResources.GuildCoin,
            }
          })
        )
      })
    }
  }, [currentGuild])

  return (
    <BasicLayout>
      <TitlePanel>유저 관리</TitlePanel>
      <Panel>
        <ServerSearch isOnlyServer />
        <Divider />
        <GuildSearch
          defaultSearchValue={pid}
          onSearch={(searchType, searchValue) => {
            getGuildList(1, {
              query: {[searchType]: {$eq: searchValue}},
            })
          }}
        />
      </Panel>
      {currentGuild && (
        <Content>
          <Descriptions bordered size={'small'} labelStyle={{width: 130}}>
            <Descriptions.Item label="길드명">
              {currentGuild.guildName}
            </Descriptions.Item>
            <Descriptions.Item label="길드 레벨">
              {currentGuild.guildLevel}
            </Descriptions.Item>
            <Descriptions.Item label="누적 공헌도">
              {currentGuild.exp}
            </Descriptions.Item>
            <Descriptions.Item label="길드 마스터">
              {currentGuild.master}
            </Descriptions.Item>
            <Descriptions.Item label="길드 인원">
              {currentGuild.memberCount}
            </Descriptions.Item>
            <Descriptions.Item label="생성 일자">
              {currentGuild.createdAt}
            </Descriptions.Item>
          </Descriptions>
        </Content>
      )}
      {currentGuild && <Table columns={columns} dataSource={members} />}
    </BasicLayout>
  )
}

export default observer(GuildPage)
