import {axiosApi} from '../utils/axios'
import axios from 'axios'
import {SeverType} from '../stores/AppStore'

interface IGuildParams {
  wid: string
  justCount: boolean
  projection?: string
  query?: any
  skip?: number
  limit?: number
}

export const SERVER_URL: any = {
  unity: 'http://15.164.102.135',
  test: 'http://13.125.237.231',
  release: 'https://dr-game.ex4games.com',
}

export const getHelloServer = (serverType: SeverType) => {
  return axios.get(`${SERVER_URL[serverType]}:30001/hello`)
}

export const getServers = () => {
  return axiosApi({url: '/servers/show', method: 'get'})
}

export const getWorldServers = () => {
  return axiosApi({url: '/servers/showWorlds', method: 'get'})
}

export const getChannels = (wid: string) => {
  return axiosApi({url: '/servers/showChannels', method: 'get', params: {wid}})
}

export const createChannel = () => {
  return axiosApi({url: '/servers/createChannel', method: 'put'})
}

export const command = (target: string, command: string) => {
  return axiosApi({
    url: '/servers/command',
    method: 'put',
    params: {target: `world-${target}`, command},
  })
}

export const getGuilds = (params: IGuildParams) => {
  return axiosApi({
    url: '/servers/findGuilds',
    method: 'get',
    params: {...params, projection: 'pid,data,submaster'},
  })
}
