import React, {useEffect, useState} from 'react'
import {Link} from 'react-router-dom'
import {observer} from 'mobx-react-lite'
import BasicLayout from '../../components/Layout/BasicLayout'
import TitlePanel from '../../components/Layout/TitlePanel'
import Content from '../../components/Layout/Content'
import ServerSearch from '../../components/Search/ServerSearch'
import {useStores} from '../../stores'
import {Table} from 'antd'
import GuildModel from '../../stores/models/GuildModel'
import {numberWithCommas} from '../../utils/number'

const GuildsPage = () => {
  const [selectedRowModels, setSelectedRowModels] = useState<GuildModel[]>([])
  const {
    appStore: {
      server: {wid},
    },
    playerStore: {getGuildList, guildList, guildCount, pageInfo},
  } = useStores()

  const columns = [
    {title: '넘버', dataIndex: 'number'},
    {
      title: '길드명',
      dataIndex: 'guildName',
      render: (v: string) => (
        <a
          target={'_blank'}
          href={`/dragonraja-admin/#/player/guild/${v || ''}`}
        >
          {v}
        </a>
      ),
    },
    {title: '마스터', dataIndex: 'master'},
    {title: '레벨', dataIndex: 'guildLevel'},
    {title: '누적 길드 공헌도', dataIndex: 'exp'},
    {title: '창설일', dataIndex: 'createdAt'},
    {title: '인원현황', dataIndex: 'memberCount'},
  ]

  useEffect(() => {
    if (wid) {
      getGuildList()
    }
  }, [wid])

  return (
    <BasicLayout>
      <TitlePanel>회원 관리</TitlePanel>
      <Content>
        <ServerSearch />
        <div style={{marginTop: 20}}>
          <div style={{textAlign: 'right', marginBottom: 5}}>
            전체 길드 {numberWithCommas(guildCount)}개
          </div>
          <Table
            rowKey={'_id'}
            bordered
            columns={columns}
            dataSource={guildList}
            pagination={{
              position: ['bottomCenter'],
              total: guildCount,
              pageSize: pageInfo.size,
              onChange: (page) => {
                getGuildList(page)
              },
            }}
            rowSelection={{
              preserveSelectedRowKeys: true,
              onChange: (
                selectedRowKeys: React.Key[],
                selectedRowModels: GuildModel[]
              ) => {
                setSelectedRowModels(selectedRowModels)
              },
            }}
          />
        </div>
      </Content>
    </BasicLayout>
  )
}

export default observer(GuildsPage)
